body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

form.form{
    height: 30px;
    margin-top: 20px;
    text-align: center;
}

form.form div.inputSection{
    width: 33%;
    height: 100%;
    position: relative;
    margin: 0 auto;
}

form.form input.input{
    border-radius: 5px;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    height: 100%;
    width: 100%;
    border-color:rgba(0,0,0,0.5);
    padding: 0 0 0 5px;

}

form.form input.input:focus{
    border-color:rgba(0,0,0,1);
}

form.form i{
    position: absolute;
    cursor: pointer;
    top: 50%;
    right: 2%;
    background:white;
    -webkit-transform: translate(0,-50%);
            transform: translate(0,-50%);
}

form.form p.error{
    color: red;
    font-size: 14px;
    
}


/* Resizing */

@media(max-width:992px){
    form.form div.inputSection{
        width: 50%;
    }
}

@media(max-width:768px){
    form.form div.inputSection{
        width: 75%;
    }
}

@media(max-width:576px){
    form.form div.inputSection{
        width: 100%;
    }
}
div.imgContainer{
    text-align: center;
    margin-top: 7.5%;
}

div.imgContainer img{
    width: 10vw;
}

div.gifContainer{
    padding: 0.5%;
    height: 250px;
    margin: 2.5% 0;
    box-shadow: -6px 18px 12px 0px rgba(0,0,0,0.3);
    text-align: center;
}

div.gifContainer img.gif{
    height: 100%;
    width: 100%;
    margin-top: 0;
}

div.gifContainer img{
    width: 15%;
    height: 15%;
    margin-top: -87.5%;
}

div.row{
    margin-top: 1%
}

div.row div.row{
    padding: 0;
    margin: 1% auto 0;
}

p.err{
    text-align: center;
    margin-top: 5%;
    color: red;
    font-size: 20px;
    font-weight: 500;
}

/* Resizing */
@media(max-width:576px){
    div.gifContainer{
        height: 225px;
    }
}
